import React, { useState, useEffect } from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { makeStyles } from '@material-ui/core/styles';
import Container from "@material-ui/core/Container";
import Toolbar from "@material-ui/core/Toolbar";

import storeData from "../resource/StoreData";

import beanBackground from "../resource/img/bean-bg.png";
import zBeansLogo from "../resource/img/logo720.png";
import sustainableImage from "../resource/img/sustainable-solutions.png";

const useStyles = makeStyles((theme) => ({
	container: {
		// backgroundImage: `url(${beanBackground})`,
		backgroundRepeat: "no-repeat",
		// backgroundPosition: "0px 250px",
		backgroundSize: "cover",
	},
	storeButton: {
		fontFamily: "'Josefin Slab', serif",
		fontWeight: 600,
		fontSize: "1.5em",
		lineHeight: "1em",
		color: "#ffffff",
		backgroundColor: "transparent",
		border: "1px solid #ffffff",
		borderRadius: "0.5em",
		padding: "0.75em",
		margin: "0.75em 0",
		cursor: "pointer",
		overflow: "hidden",
		"&:hover": {
			backgroundColor: "#ffffff",
			color: "saddlebrown",
		}
	},
	location: {
		fontSize: "0.8em",
		fontWeight: 500,
		whiteSpace: "nowrap",
	},
	socialButton: {
		padding: "0.7em",
		fontSize: "1.7em",
		textAlign: "center",
		textDecoration: "none",
		margin: "0.5em",
		background: "#42201e",
		color: "white",
		borderRadius: "12px",
		"&:hover": { 
			opacity: 0.7,
		}
	}
}));

type Data = {
	site: {
		siteMetadata: {
			title: string
		}
	}
}

const HomeLayout = ({ location, title, children }) => {

	return (
		<Layout location={location} title={title}>
			<div
				style={{
					height: "100vh",
				}}
				className="homeLayout"
			>
				<main
					style={{
						padding: `0`,
						height: "100%",
					}}
				>
					{children}
				</main>
			</div>
		</Layout>
	)
}

const HomePage = ({ data, location }: PageProps<Data>) => {
	const siteTitle = data.site.siteMetadata.title

	const classes = useStyles();

	return (
		<HomeLayout location={location} title={siteTitle}>
			<SEO title="Home" />
			<div className={classes.container} style={{display: "flex", flexDirection: "column", height: '100%', overflowY: "auto"}}>
				<Container maxWidth="sm" style={{display: "flex", flexDirection: "column", padding: "0"}}>
					<div style={{backgroundColor: "#ffffff", borderRadius: "50%", width: "8em", height: "8em", margin: "1em auto"}}>
						<img src={zBeansLogo} alt="" style={{maxWidth: "100%"}} />
					</div>
					<h1 style={{color: "#42201e", textAlign: "center", marginTop: 0}}>Welcome to Z Beans!</h1>
					<img alt="sustainable solutions" src={sustainableImage} style={{marginBottom: 0}} />
					<iframe 
						width="560"
						height="315"
						src="https://www.youtube.com/embed/8MMxeoA5E18"
						frameBorder="0"
						allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						allowFullScreen
						style={{
							maxWidth: "100%"
						}}
					>
					</iframe>
				</Container>
				<Container maxWidth="sm" style={{display: "flex", justifyContent: "space-around"}} >
					<a href="https://www.facebook.com/ZBeansCoffee/" className={classes.socialButton + " fa fa-facebook"}></a>
					<a href="https://twitter.com/ZBeansCoffee" className={classes.socialButton + " fa fa-twitter"}></a>
					<a href="https://www.instagram.com/zbeanscoffee/" className={classes.socialButton + " fa fa-instagram"}></a>
					<a href="https://www.youtube.com/channel/UCdEeXNPmtiPDI5vDxuhSXAw" className={classes.socialButton + " fa fa-youtube"}></a>
				</Container>
				<Toolbar></Toolbar>
			</div>
		</HomeLayout>
	)
}

export default HomePage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
